html {
  font-size: 16px;
}
.main-page {
  padding: 0.0625rem 1.875rem 1.25rem 1.875rem;
  height: 100%;
  position: relative;
}
.main-page.img {
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 40%;
}
.main-page header .page-title {
  color: #0F296D;
  font-family: 'MyCustomFont';
  text-align: left;
  font-size: 2.4rem;
  font-weight: 600;
  margin: 0 0 0.625rem 0;
}
.main-card {
  background-color: rgba(186, 222, 162, 0.264);
  padding: 1.25rem;
  border-radius: 0.5rem;
}
.subtitle {
  color: #0F296D;
  font-family: 'MyCustomFont';
  font-weight: 600;
  text-align: left;
  font-size: 1.6rem;
}
.info-list {
  color: #333;
  text-align: left;
}
.info-list .info-item {
  margin-bottom: 1.25rem;
}
.info-details {
  font-size: 1rem;
  font-family: 'MyCustomFont';
}
.description-list {
  list-style-type: disc;
  margin-left: 1.25rem;
  font-size: 1rem;
  font-family: 'MyCustomFont';
}
.main-btn {
  padding: 0.625rem;
  font-size: 1.5rem;
  font-family: 'MyCustomFont';
  background-color: #0F296D;
  color: #fff;
  min-width: 7.5rem;
  text-align: center;
}
.size-btn {
  font-size: 1.5rem;
  color: #0F296D;
  font-family: 'MyCustomFont';
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.size-btn:hover {
  transform: scale(1.3);
}
.zigzag-card {
  width: 70%;
}
.zigzag-card:nth-child(even) {
  float: right;
}
.fork-common.main-page {
  background-color: #f5f7fa;
  min-height: 100vh;
  padding: 40px 0;
}
.fork-common.main-page .game-card {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
  transition: all 0.3s ease;
}
.fork-common.main-page .game-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}
.fork-common.main-page .intro-text {
  font-size: 1.3rem;
  margin-bottom: 30px;
  line-height: 1.6;
  font-weight: 500;
  color: #34495e;
}
.fork-common.main-page .step-container {
  margin-bottom: 40px;
}
.fork-common.main-page .step-image {
  width: 100%;
  max-height: 500px;
  object-fit: contain;
  border-radius: 12px;
  margin-bottom: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}
.fork-common.main-page .step-text {
  font-size: 1.1rem;
  margin-bottom: 20px;
  line-height: 1.6;
  color: #34495e;
}
.fork-common.main-page .step-divider {
  margin: 30px 0;
  border-top: 1px solid rgba(52, 73, 94, 0.1);
}
.fork-common.main-page .choices {
  margin-top: 40px;
}
.fork-common.main-page .choices h2 {
  font-size: 1.5rem;
  margin-bottom: 30px;
  line-height: 1.4;
  color: #1DA57A;
  text-align: center;
}
.fork-common.main-page .choice-card {
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.fork-common.main-page .choice-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}
.fork-common.main-page .choice-card .choice-image {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.fork-common.main-page .choice-card .choice-text {
  font-size: 1.1rem;
  padding: 20px;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 500;
  color: #34495e;
  background-color: rgba(52, 152, 219, 0.1);
}
.fork-common.main-page .navigation-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.fork-common.main-page .navigation-buttons button {
  min-width: 120px;
  height: 40px;
  font-size: 1rem;
  border-radius: 20px;
}
.fork-common.main-page .navigation-buttons button.ant-btn-primary {
  background-color: #1DA57A;
  border-color: #1DA57A;
}
.fork-common.main-page .text-center {
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 30px 0;
  color: #1DA57A;
}
.fork-common.main-page .final-card .afp-badge {
  width: 100%;
  max-width: 300px;
  display: block;
  margin: 0 auto 30px;
}
.fork-common.main-page .final-card ul {
  padding-left: 20px;
  margin-bottom: 20px;
}
.fork-common.main-page .final-card .ant-typography {
  margin-bottom: 20px;
  color: #34495e;
}
.fork-common.main-page .final-card a {
  color: #1DA57A;
  text-decoration: underline;
}
.fork-common.main-page .final-card a:hover {
  color: #157a5a;
}
.fork-common.main-page .final-card .full-width-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}
.fork-common.main-page .start-button-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.fork-common.main-page.transition {
  animation: timeTravel 2.5s forwards;
}
@keyframes timeTravel {
  0% {
    filter: blur(0);
    transform: scale(1) rotate(0);
    opacity: 1;
  }
  25% {
    filter: blur(15px);
    transform: scale(1.1) rotate(2deg);
    opacity: 0.8;
  }
  50% {
    filter: blur(30px);
    transform: scale(1.2) rotate(-2deg);
    opacity: 0.6;
  }
  75% {
    filter: blur(15px);
    transform: scale(1.1) rotate(2deg);
    opacity: 0.8;
  }
  100% {
    filter: blur(0);
    transform: scale(1) rotate(0);
    opacity: 1;
  }
}
@media (max-width: 768px) {
  .fork-common.main-page {
    padding: 20px 0;
  }
  .fork-common.main-page .game-card {
    padding: 20px;
  }
  .fork-common.main-page .intro-text {
    font-size: 1.1rem;
  }
  .fork-common.main-page .step-image {
    max-height: 400px;
  }
  .fork-common.main-page .step-text {
    font-size: 1rem;
  }
  .fork-common.main-page .choices h2 {
    font-size: 1.3rem;
  }
  .fork-common.main-page .choice-card {
    margin-bottom: 20px;
  }
  .fork-common.main-page .choice-card .choice-image {
    height: 200px;
  }
  .fork-common.main-page .choice-card .choice-text {
    font-size: 1rem;
    padding: 15px;
  }
}
@media (max-width: 576px) {
  .fork-common.main-page .intro-text {
    font-size: 1rem;
  }
  .fork-common.main-page .step-image {
    max-height: 300px;
  }
  .fork-common.main-page .step-text {
    font-size: 0.9rem;
  }
  .fork-common.main-page .choices h2 {
    font-size: 1.1rem;
  }
}
