    color: #00BD90 !important;
}
.ant-tabs-ink-bar{
    background: #00BD90 !important;
}

.text-center {
    text-align: center;
  }
  
  .map-container {
    height: 600px;
    width: 70%;
    margin: 20px auto 0 auto;
  }
  
  .leaflet-container {
    height: 100%;
    width: 100%;
  }
  
  .custom-marker-icon {
    background-color: #00BD90;
    border-radius: 50%;
    border: 2px solid white;
  }
  
  .ant-tabs-tab-btn {
    color: #00BD90 !important;
  }
  
  .ant-tabs-ink-bar {
    background: #00BD90 !important;
  }
  
  .flex {
    display: flex;
  }
  
  .justify-center {
    justify-content: center;
  }
  
  .gap-4 {
    gap: 1rem;
  }
  
  .mt-\[40px\] {
    margin-top: 40px;
  }
  
  .w-\[300px\] {
    width: 300px;
  }
  
  .bg-\[#00BD90\] {
    background-color: #00BD90;
  }
  
  .hover\:bg-\[#00A77D\]:hover {
    background-color: #00A77D;
  }
  
  .text-white {
    color: white;
  }
  
  @media (max-width: 768px) {
    .map-container {
      height: 300px;
    }
  
    .w-\[300px\] {
      width: 100%;
    }
  
    .flex {
      flex-direction: column;
    }
  
    .gap-4 {
      gap: 0.5rem;
    }
  }
  
  .leaflet-popup-content-wrapper {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 4px;
    box-shadow: 0 3px 14px rgba(0,0,0,0.4);
  }
  
  .leaflet-popup-content {
    margin: 13px 19px;
    line-height: 1.4;
  }
  
  .ant-spin-lg .ant-spin-dot {
    font-size: 32px;
  }
  
  .ant-spin-lg .ant-spin-dot i {
    width: 14px;
    height: 14px;
  }
  
  .legend {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 10px;
    background: white;
    line-height: 18px;
    color: #555;
    border-radius: 5px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
    z-index: 1000;
}

.legend h4 {
    margin: 0 0 5px;
    color: #333;
}

.legend i {
    width: 18px;
    height: 18px;
    float: left;
    margin-right: 8px;
    opacity: 0.7;
}